@import url(https://fonts.googleapis.com/css2?family=Syne:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway:200);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{width:100vw;height:100vw;font-family:'Syne', sans-serif;overflow:hidden;padding:0;margin:0;background-color:black}body,html{height:100%;margin:0;font-weight:200}body{background-color:#0f0f1a;display:flex;align-items:center;justify-content:center;flex-direction:column}.pass-page{font-family:'Raleway', sans-serif}.digit-group input{width:50px;height:50px;background-color:#18182a;border:none;line-height:50px;text-align:center;font-size:24px;font-family:'Raleway', sans-serif;font-weight:200;color:white;margin:0 2px}.digit-group .splitter{padding:0 5px;color:white;font-size:24px}.prompt{margin-bottom:20px;font-size:20px;color:white}.image-border{align-items:center;margin-top:5em;margin-left:5.5em;display:flex}img{vertical-align:middle}.tooltip{position:relative;display:inline-block;border-bottom:1px dotted #e9b1ff}.tooltip .tooltiptext{visibility:hidden;width:120px;background-color:#ffdafd;color:#292323;text-align:center;padding:5px 0;border-radius:6px;position:absolute;z-index:1}.tooltip:hover .tooltiptext{visibility:visible}.tooltip .tooltiptext{width:100px;bottom:90%;margin-bottom:-7.4rem;left:20%;margin-left:-110px}@-webkit-keyframes fade{from{opacity:.4}to{opacity:1}}@keyframes fade{from{opacity:.4}to{opacity:1}}@media only screen and (max-width: 300px){.prev,.next,.text{font-size:11px}}

